}

.program-card__likes .extra {
    @apply text-xs font-normal ml-2 whitespace-nowrap hidden
}

.program-card__likes:hover .extra {
    @apply block
}

.swiper.js-edu-slider {
    overflow: visible;
}

.js-edu-slider-pagination.swiper-pagination-progressbar,
.js-edu-slider-scrollbar.swiper-scrollbar {
