.way-tab:nth-child(2).active {
    @apply after:bg-color-blue
}

.way-tab:nth-child(3).active svg path,
.way-tab:nth-child(3):hover svg path {
    @apply fill-color-orange
}

.way-tab:nth-child(3).active {
    @apply after:bg-color-orange
}

.way-tab:nth-child(4).active svg path,
.way-tab:nth-child(4):hover svg path {
    @apply fill-color-green
}

.way-tab:nth-child(4).active {
    @apply after:bg-color-green
}

.way-wrapper .container {
    @apply max-w-[1062px] mx-auto flex items-center justify-between -md:flex-col
}

.way-title {
    @apply font-bold
}

.swiper.js-way-slider {
    @apply overflow-visible
} 

