}

.review-wrapper {
    box-shadow: 0px 30px 50px rgba(15, 144, 209, 0.1);
    @apply py-8 px-16 w-full max-w-[700px] mx-auto bg-white rounded-[70px] flex flex-col items-center -sm:px-6 -sm:rounded-[20px]
}

.review__title {
    @apply text-xl leading-6 text-color-dark font-bold mb-2 text-center -sm:text-base
}

.start-rating {
