#map { 
    @apply h-[720px] -md:h-[100vh]
}

.map-popup {
    @apply absolute right-[16.2vw] py-12 pl-12 pr-6 bg-white rounded-[20px] z-[9999999999] w-11/12 max-w-[360px] transition-all top-32 opacity-0 invisible -sm:right-auto -sm:left-1/2  -sm:-translate-x-1/2 
}

.map-popup.active {
    @apply top-[95px] opacity-100 visible;
